<template>
  <view class="history-page">
    <!-- È°∂ÈÉ®Ê†áÈ¢òÊ†è -->
    <view class="header">
      <text class="header-title">ÂéÜÂè≤ÊµèËßà</text>
      <text class="clear-btn" @click="clearHistory" v-if="activeTab === 0">Ê∏ÖÁ©∫</text>
    </view>

    <!-- ÈÄâÈ°πÂç° -->
    <view class="tabs-container">
      <view 
        class="tab-item" 
        v-for="(tab, index) in tabsWithCount" 
        :key="index"
        :class="{ active: activeTab === index }"
        @click="switchTab(index)"
      >
        <view class="tab-content">
          <text class="tab-name">{{ tab.name }}</text>
          <text class="tab-count">{{ tab.count }}</text>
        </view>
        <view v-if="activeTab === index" class="tab-indicator"></view>
      </view>
    </view>

    <!-- ÂïÜÂìÅÂàóË°® -->
    <scroll-view scroll-y class="goods-list">
      <view class="goods-grid">
        <view 
          class="goods-item" 
          v-for="item in currentGoodsList" 
          :key="item.goods_id"
          @click="gotoDetail(item.goods_id)"
        >
          <image :src="item.goods_big_logo" class="goods-image" mode="aspectFill"></image>
          <view class="goods-price">
            <text class="price-symbol">¬•</text>
            <text class="price-number">{{ item.goods_price }}</text>
          </view>
        </view>
      </view>

      <!-- Á©∫Áä∂ÊÄÅ -->
      <view v-if="currentGoodsList.length === 0" class="empty-state">
        <text class="empty-text">ÊöÇÊó†{{ tabs[activeTab].name }}ËÆ∞ÂΩï</text>
      </view>
    </scroll-view>
  </view>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data() {
    return {
      activeTab: 0,
      tabs: [
        { name: 'ÊàëÊµèËßàÁöÑ', type: 'viewed' },
        { name: 'ÊàëÊî∂ËóèÁöÑ', type: 'collected' },
        { name: 'ÊàëÁïôË®ÄÁöÑ', type: 'commented' }
      ],
      
      // ‚úÖ Â≠òÂÇ®ÊØè‰∏™ tab ÁöÑÂïÜÂìÅ ID ÂàóË°®
      viewedIds: [],
      collectedIds: [],
      commentedIds: [],
      
      // ‚úÖ Â≠òÂÇ®ÊØè‰∏™ tab ÁöÑÂïÜÂìÅËØ¶ÊÉÖÂàóË°®
      viewedGoodsList: [],
      collectedGoodsList: [],
      commentedGoodsList: [],
    };
  },

  computed: {
    ...mapState('m_user', ['token', 'code', 'userBase', 'openid']),

    // ‚úÖ ËÆ°ÁÆóÊØè‰∏™ tab ÁöÑÊï∞Èáè
    tabsWithCount() {
      return [
        {
          name: 'ÊàëÊµèËßàÁöÑ',
          count: this.viewedGoodsList.length
        },
        {
          name: 'ÊàëÊî∂ËóèÁöÑ',
          count: this.collectedGoodsList.length
        },
        {
          name: 'ÊàëÁïôË®ÄÁöÑ',
          count: this.commentedGoodsList.length
        }
      ];
    },

    // ‚úÖ Ê†πÊçÆÂΩìÂâç tab ËøîÂõûÂØπÂ∫îÁöÑÂïÜÂìÅÂàóË°®
    currentGoodsList() {
      switch(this.activeTab) {
        case 0: return this.viewedGoodsList;
        case 1: return this.collectedGoodsList;
        case 2: return this.commentedGoodsList;
        default: return [];
      }
    },
  },

  async onLoad() {
    // ‚úÖ È°µÈù¢Âä†ËΩΩÊó∂ÂêåÊó∂Ëé∑ÂèñÊâÄÊúâ tab ÁöÑÊï∞ÊçÆ
    await this.loadAllTabsData();
  },

  methods: {
    // ‚úÖ ÂêåÊó∂Âä†ËΩΩÊâÄÊúâ tab ÁöÑÊï∞ÊçÆ
    async loadAllTabsData() {
      try {
        uni.showLoading({ title: 'Âä†ËΩΩ‰∏≠...' });
        
        // Âπ∂Ë°åËØ∑Ê±ÇÊâÄÊúâÊï∞ÊçÆ
        await Promise.all([
          this.loadTabData(0),  // ÊàëÊµèËßàÁöÑ
          this.loadTabData(1),  // ÊàëÊî∂ËóèÁöÑ
          this.loadTabData(2)   // ÊàëÁïôË®ÄÁöÑ
        ]);
        
        console.log('[History] ÊâÄÊúâÊï∞ÊçÆÂä†ËΩΩÂÆåÊàê');
        console.log('  - ÊµèËßà:', this.viewedGoodsList.length);
        console.log('  - Êî∂Ëóè:', this.collectedGoodsList.length);
        console.log('  - ÁïôË®Ä:', this.commentedGoodsList.length);
        
      } catch (error) {
        console.error('[History] Âä†ËΩΩÊï∞ÊçÆÂ§±Ë¥•:', error);
        uni.showToast({
          title: 'Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        });
      } finally {
        uni.hideLoading();
      }
    },

    // ‚úÖ Âä†ËΩΩÊåáÂÆö tab ÁöÑÊï∞ÊçÆ
    async loadTabData(tabIndex) {
      try {
        const queryObj = { code: this.openid };
        
        // 1. Ëé∑ÂèñÂïÜÂìÅ ID ÂàóË°®
        let idsResponse;
        switch(tabIndex) {
          case 0: 
            idsResponse = await uni.$http.get('/users/history', queryObj);
            break;
          case 1: 
            idsResponse = await uni.$http.get('/users/collect', queryObj);
            break;
          case 2: 
            idsResponse = await uni.$http.get('/users/comment', queryObj);
            break;
        }
        
        if (idsResponse.data.meta.status !== 200) {
          throw new Error(`Ëé∑Âèñ${this.tabs[tabIndex].name}ÂàóË°®Â§±Ë¥•`);
        }
        
        const ids = idsResponse.data.data || [];
        
        // ‰øùÂ≠ò ID ÂàóË°®
        switch(tabIndex) {
          case 0: this.viewedIds = ids; break;
          case 1: this.collectedIds = ids; break;
          case 2: this.commentedIds = ids; break;
        }
        
        // 2. Â¶ÇÊûúÊúâ IDÔºåÂàôËé∑ÂèñÂïÜÂìÅËØ¶ÊÉÖ
        if (ids.length > 0) {
          const goodsResponse = await uni.$http.get('/goods/historysearch', { 
            goods_id: ids 
          });
          
          if (goodsResponse.data.meta.status === 200) {
            const goodsList = goodsResponse.data.message || [];
            
            // ‰øùÂ≠òÂïÜÂìÅËØ¶ÊÉÖÂàóË°®
            switch(tabIndex) {
              case 0: this.viewedGoodsList = goodsList; break;
              case 1: this.collectedGoodsList = goodsList; break;
              case 2: this.commentedGoodsList = goodsList; break;
            }
          }
        } else {
          // Ê≤°ÊúâÊï∞ÊçÆÊó∂Ê∏ÖÁ©∫ÂàóË°®
          switch(tabIndex) {
            case 0: this.viewedGoodsList = []; break;
            case 1: this.collectedGoodsList = []; break;
            case 2: this.commentedGoodsList = []; break;
          }
        }
        
      } catch (error) {
        console.error(`[History] Âä†ËΩΩ${this.tabs[tabIndex].name}Â§±Ë¥•:`, error);
        // Âä†ËΩΩÂ§±Ë¥•Êó∂ËÆæÁΩÆ‰∏∫Á©∫Êï∞ÁªÑ
        switch(tabIndex) {
          case 0: this.viewedGoodsList = []; break;
          case 1: this.collectedGoodsList = []; break;
          case 2: this.commentedGoodsList = []; break;
        }
      }
    },
    
    // ‚úÖ ÂàáÊç¢ÈÄâÈ°πÂç°ÔºàÂ∑≤ÊúâÊï∞ÊçÆ‰∏çÈáçÊñ∞Âä†ËΩΩÔºâ
    async switchTab(index) {
      console.log(`[History] ÂàáÊç¢Âà∞ tab: ${this.tabs[index].name}`);
      this.activeTab = index;
      
      // Â¶ÇÊûúÂΩìÂâç tab ËøòÊ≤°ÊúâÊï∞ÊçÆÔºåÂàôÂä†ËΩΩ
      const currentList = this.currentGoodsList;
      if (currentList.length === 0) {
        console.log(`[History] ${this.tabs[index].name} ÊöÇÊó†Êï∞ÊçÆÔºåÈáçÊñ∞Âä†ËΩΩ`);
        await this.loadTabData(index);
      }
    },
    
    // ‚úÖ Ê∏ÖÁ©∫ÂéÜÂè≤ËÆ∞ÂΩï
    async clearHistory() {
      const tabName = this.tabs[this.activeTab].name;
      
      uni.showModal({
        title: 'ÊèêÁ§∫',
        content: `Á°ÆÂÆöË¶ÅÊ∏ÖÁ©∫ÊâÄÊúâ${tabName}ÂêóÔºü`,
        success: async (res) => {
          if (res.confirm) {
            try {
              uni.showLoading({ title: 'Ê∏ÖÁ©∫‰∏≠...' });
              
              // Ë∞ÉÁî®ÂêéÁ´ØÊ∏ÖÁ©∫Êé•Âè£
              const { data: response } = await uni.$http.post('/users/clearhistory', { 
                code: this.openid,
                type: this.activeTab  // 0=ÊµèËßà, 1=Êî∂Ëóè, 2=ÁïôË®Ä
              });
              
              if (response.meta.status === 200) {
                // Ê∏ÖÁ©∫ÂØπÂ∫îÁöÑÊï∞ÊçÆ
                switch(this.activeTab) {
                  case 0: 
                    this.viewedIds = [];
                    this.viewedGoodsList = [];
                    break;
                  case 1: 
                    this.collectedIds = [];
                    this.collectedGoodsList = [];
                    break;
                  case 2: 
                    this.commentedIds = [];
                    this.commentedGoodsList = [];
                    break;
                }
                
                uni.showToast({
                  title: 'Â∑≤Ê∏ÖÁ©∫',
                  icon: 'success'
                });
              } else {
                throw new Error(response.meta.msg || 'Ê∏ÖÁ©∫Â§±Ë¥•');
              }
              
            } catch (error) {
              console.error('[History] Ê∏ÖÁ©∫Â§±Ë¥•:', error);
              uni.showToast({
                title: 'Ê∏ÖÁ©∫Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
                icon: 'none'
              });
            } finally {
              uni.hideLoading();
            }
          }
        }
      });
    },
    
    // ‚úÖ Ë∑≥ËΩ¨Âà∞ÂïÜÂìÅËØ¶ÊÉÖ
    async gotoDetail(goods_id) {
		try {
		  // 1. ÂÖàÂ¢ûÂä†ÊµèËßàÊ¨°Êï∞
		  console.log('üìä Â¢ûÂä†ÂïÜÂìÅÊµèËßàÊ¨°Êï∞:', goods_id);
		  const { data: res } = await uni.$http.post('/goods/view', {
		    goods_id: goods_id,
		    // ÂèØÈÄâÔºöÊ∑ªÂä†Áî®Êà∑‰ø°ÊÅØÁî®‰∫éÁªüËÆ°ÂàÜÊûê
		    user_openid: this.openid || null
		  });
		
		  if (res.meta.status === 200) {
		    console.log('‚úÖ ÊµèËßàÊ¨°Êï∞Â¢ûÂä†ÊàêÂäü');
		  } else {
		    console.warn('‚ö†Ô∏è ÊµèËßàÊ¨°Êï∞Â¢ûÂä†Â§±Ë¥•:', res.meta.msg);
		  }
		} catch (error) {
		  console.error('‚ùå ÊµèËßàÊ¨°Êï∞Â¢ûÂä†Êé•Âè£ÈîôËØØ:', error);
		  // Âç≥‰ΩøÊé•Âè£Â§±Ë¥•‰πüÁªßÁª≠Ë∑≥ËΩ¨Ôºå‰∏çÂΩ±ÂìçÁî®Êà∑‰ΩìÈ™å
		}
      if (!goods_id) {
        console.warn('[History] ÂïÜÂìÅ ID Êó†Êïà');
        return;
      }
      
      uni.navigateTo({
        url: `/subpkg/goods_detail/goods_detail?goods_id=${goods_id}`
      });
    },
    
    // ‚úÖ Âà∑Êñ∞ÂΩìÂâç tab ÁöÑÊï∞ÊçÆÔºàÂèØÈÄâÔºåÁî®‰∫é‰∏ãÊãâÂà∑Êñ∞Ôºâ
    async refreshCurrentTab() {
      await this.loadTabData(this.activeTab);
    },
    
    // ‚úÖ Âà∑Êñ∞ÊâÄÊúâ tab ÁöÑÊï∞ÊçÆÔºàÂèØÈÄâÔºâ
    async refreshAllTabs() {
      await this.loadAllTabsData();
    }
  },
};
</script>

<style lang="scss">
.history-page {
  width: 100%;
  height: 100vh;
  background: #f5f5f5;
  display: flex;
  flex-direction: column;
}

// È°∂ÈÉ®Ê†áÈ¢òÊ†è
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 28rpx 32rpx;
  background: #fff;
  border-bottom: 1rpx solid #e5e5e5;

  .header-title {
    font-size: 36rpx;
    font-weight: 700;
    color: #333;
  }

  .clear-btn {
    font-size: 28rpx;
    color: #ff4444;
    padding: 8rpx 16rpx;
    transition: opacity 0.3s;
    
    &:active {
      opacity: 0.6;
    }
  }
}

// ÈÄâÈ°πÂç°ÂÆπÂô®
.tabs-container {
  display: flex;
  background: #fff;
  padding: 0 32rpx;
  border-bottom: 1rpx solid #e5e5e5;

  .tab-item {
    flex: 1;
    text-align: center;
    padding: 24rpx 0;
    position: relative;
    transition: all 0.3s;

    .tab-content {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 8rpx;

      .tab-name {
        font-size: 28rpx;
        color: #666;
        transition: all 0.3s;
      }

      .tab-count {
        font-size: 20rpx;
        color: #999;
        background: #f5f5f5;
        padding: 2rpx 10rpx;
        border-radius: 12rpx;
        min-width: 32rpx;
        text-align: center;
        transition: all 0.3s;
      }
    }

    &.active {
      .tab-content {
        .tab-name {
          color: #333;
          font-weight: 600;
        }

        .tab-count {
          color: #fff;
          background: #ff4444;
        }
      }

      .tab-indicator {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 60rpx;
        height: 4rpx;
        background: #ff4444;
        border-radius: 2rpx;
      }
    }
  }
}

// ÂïÜÂìÅÂàóË°®
.goods-list {
  flex: 1;
  padding: 20rpx;
}

.goods-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.goods-item {
  width: 48%;
  background: #fff;
  border-radius: 12rpx;
  overflow: hidden;
  margin-bottom: 20rpx;
  box-shadow: 0 2rpx 12rpx rgba(0, 0, 0, 0.06);
  transition: transform 0.3s;

  &:active {
    transform: scale(0.98);
  }

  .goods-image {
    width: 100%;
    height: 340rpx;
    display: block;
    background: #f0f0f0;
  }

  .goods-price {
    padding: 16rpx 20rpx;
    display: flex;
    align-items: baseline;

    .price-symbol {
      font-size: 24rpx;
      color: #ff4444;
      font-weight: 600;
    }

    .price-number {
      font-size: 36rpx;
      color: #ff4444;
      font-weight: 700;
      margin-left: 4rpx;
    }
  }
}

// Á©∫Áä∂ÊÄÅ
.empty-state {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 600rpx;

  .empty-text {
    font-size: 28rpx;
    color: #999;
  }
}
</style>